<template>
  <div class="goods-details">
    <!-- 商品详情页/{{ $route.params.id }} -->
    商品详情页/{{id}}
    <div class="go-back" @click="goBack">
      返回
    </div>
  </div>
</template>

<script>
//query方式 /detail?/1
//params方式 /detail/1 这种需要在路由中定义
// 需求商品详情页的底部导航tabbar是不展示的
import {reactive,toRefs,onMounted,onUnmounted} from '@vue/composition-api'
export default {
  setup (props,{root}) {
    const state = reactive({
      // lists:[]
      // id: root.$route.params.id,
      id: '',
    })
    let goBack= () =>{
      //正常情况下每张图片都有ID,我们点击每一张图片都会传递一个ID过去，获取不同商品详情页的ID
      // root.$router.push({name:'Home'})
      // root.$router.push({path:'/'})
      root.$router.push('/')
    }
    // let getGoodsDetails= ()=>{
    //   let params = {
    //     goodsId:root.$route.query.id  //query
    //     goodsId:root.$route.params.id //params
    //   }
    //   root.$axios.get('/goodsDetail',params).then(res => {
    //     if(res.status === 200 ){
    //       stats.lists = res.data.data
    //     }
    //   }).then(error =>{
    //     console.log(error)
    //   })
    // }
    onMounted(()=>{ //挂载完成
      // getGoodsDetails()
      state.id = root.$route.params.id; 
      root.$store.dispatch('hideNav') //隐藏 刚进入的时候隐藏底部导航
    })
    onUnmounted(()=>{ //销毁 相当于 destoryed 
      // getGoodsDetails()
      root.$store.dispatch('showNav') //显示 离开的时候让其显示
    })
    return {
      ...toRefs(state),
      goBack
    }
  }
}
</script>

<style scoped>
  .goods-details {
    position:relative;
    text-align: center;
  }
  .go-back {
    width:40px;
    height:40px;
    line-height:40px;
    text-align: center;
    border-radius:50%;
    background:rgba(0,0,0,0.4);
    position:absolute;
    top:10px;
    left:10px;
    z-index:1000;
    cursor:pointer;
    user-select:none;
    overflow:hidden;
    white-space:nowrap;
    font-size:14px;
    color:#ffffff;
    letter-spacing:1px;
    font-family:'微软雅黑';
  }
</style>


